.overlay {
  background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 20%, rgba(0, 212, 255, 0) 100%);
  position: fixed;
  height: 100vh;
  width: 100vw;
  z-index: -1;
}

