.pageContainer {
  margin: 100px;
}

.PageContent {
  font-size: 2em;
  line-height: 2em;
  color: white;
  width: 700px;
  transition: 0.5s ease-in;
  text-shadow: 0.5px 0.5px black;
  max-height: 50vh;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
  overflow-y: scroll;
  /* Add the ability to scroll */
}

.PageContent::-webkit-scrollbar {
  display: none;
}

.gameTitle {
  font-weight: 800;
  font-size: 1.1em;
}

.releaseYear {
  font-weight: 100;
  font-size: .75em;
}

.positionDescription {
  font-style: italic;
  font-weight: 100;
  font-size: 0.75em;
}

.PageContent:hover {
  opacity: 1;
}

.platformLogo {
  padding: 10px 20px 5px 0px;
  display: flex;
}


.dictionary {
  /* display: flex; */
  opacity: 10%;
  transition: 0.5s;
  transition: 0.5s ease-in;
}

.dictionary:hover {
  opacity: 100%;
}

.dictionary>.term {
  margin-bottom: 60px;
  margin-right: 25px;
  border-radius: 3px;
  /* background-color: #fff; */
  transition: all 100ms ease-in-out;
  display: block;
  line-height: 0;
}

@media screen and (max-width: 825px) {
  .dictionary>.term {
    margin-bottom: 15%;
  }
}

.contentTitle {
  color: white;
  margin: 20px 0px 5px;
  text-shadow: 0.5px 0.5px black;
}

.textDescription {
  font-family: 'Assistant', sans-serif;
}

.imageThumbnail {
  width: 30em;
  z-index: -1;
  transition: 0.5s ease-in;
}

.imageThumbnail:hover {
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  z-index: 1;
}

.work {
  display: block;
  font-size: 200%;
  text-align: center;
}

.slick-list {
  /* max-width: 60vw; */
  padding: 15px 0 !important;
}

.slick-track {
  width: 5000px !important;
}

.slick-slide {
  width: 500px !important;
}

.slick-prev {
  top: -25px !important;
  left: inherit !important;
  right: 50px !important;
  z-index: 1 !important;
}

.slick-prev::before {
  font-size: 30px !important;
}

.slick-next {
  z-index: 1 !important;
  right: 10px !important;
  top: -25px !important;
}

.slick-next::before {
  font-size: 30px !important;
}

/* Home */
/* ////////////////////////// */

.homePage {
  display: flex;
  height: 100vh;
}

@media (max-width:1200px) {
  .homePage {
    flex-direction: column;
    height: auto;
    align-items: center;
  }

  .homeContainer {
    margin: 100px !important;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
  }
}

.homeContainer {
  padding: 2.5%;
  background: #000000a8;
  border-radius: 25px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-content: space-around;
  flex-direction: row;
}

.homeImage {
  height: 200px;
}

/* AboutPage */
/* ///////////////////////////////////////////////////// */
.aboutPage {
  display: flex;
  margin-bottom: 50px;
}

.aboutMe {
  font-size: 1.25em;
  color: white;
  /* padding: 2em; */
  line-height: 2.25em;
}

.AboutContainer {
  padding: 2.5%;
  background: #000000a8;
  border-radius: 25px;
  width: 45%;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-content: space-around;
  flex-direction: row;
  text-align: left;
}

@media (max-width: 1200px) {
  .aboutPage {
    flex-direction: column;
  }


  .AboutContainer {
    width: 100%;
    text-align: center;
  }

  .imageContainer {
    max-width: 100% !important;
  }
}

.imageContainer {
  max-width: 50%;
  margin: 50px 0;
}

.profileImage {
  max-height: 350px;
  margin-top: 40px;
  margin-bottom: -30px;
}

.experienceIcon {
  max-width: 100px;
  max-height: 100px;
  margin: 10px;
}

.unrealEngine {
  background-color: white;
  border-radius: 50%;
}

/* Contact Page */
/* ///////////////////////////////////////////////////////// */
.feedback-input {
  color: white;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border: 2px solid #ffffff;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  outline: 0;
}

.inputTitle {
  color: white;
  font-size: 1.2em;
  padding: 10px 0 5px 0px;
  display: block;
}

.feedback-input:focus {
  border: 2px solid #9b7909;
}

textarea {
  height: 150px;
  line-height: 150%;
  resize: vertical;
}

.submitButton {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  width: 100%;
  border-radius: 5px;
  border: 0;
  color: white;
  font-size: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 0.3s;
  margin-top: -4px;
  font-weight: 700;
  pointer-events: none;
  -webkit-animation: Gradient 15s ease infinite !important;
          animation: Gradient 15s ease infinite !important;
  background: linear-gradient(117deg, rgb(0 116 165) 0%, 50%, rgb(38 131 1) 100%);
  background-size: 400% 400% !important;
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

button:disabled {
  opacity: 20% !important;
  background: #b32121 !important;
  cursor: pointer !important;
  pointer-events: none !important;
}

button:enabled {
  opacity: 50% !important;
  cursor: pointer !important;
  pointer-events: inherit !important;
}

button:enabled:hover {
  opacity: 100% !important;
}

.ContactPage {
  width: 90%;
  margin: auto;
  padding: 2.5%;
}

.informationTitle {
  color: white;
  font-size: x-large;
  padding: 20px 0 0;
}

.informationText {
  color: white;
  font-family: 'Assistant';
  font-size: 1.2em;
  letter-spacing: .1em;
}

.titleSection {
  margin: auto;
}

.ContactTitle {
  color: white;
  display: block;
  text-align: center;
  padding: 50px 0 50px
}

.tableColumn {
  vertical-align: top;
  padding: 20px 30px 20px 0;
}

.ContactContainer {
  padding: 2.5% 0;
  background: #000000a8;
  border-radius: 2.5%;
  width: 50%;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-content: space-around;
  flex-direction: row;
  text-align: left;
}

@media (max-width: 1200px) {
  .ContactContainer {
    padding: 2.5% 0;
    background: #000000a8;
    border-radius: 20px;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-content: space-around;
    flex-direction: row;
    text-align: center;
